// components/GoogleLoginButton.js
"use client";

import { FcGoogle } from "react-icons/fc";
import { useGoogleLogin } from "@react-oauth/google";
import { useRouter } from "next/navigation"; // Use 'next/navigation' for App Router

export default function GoogleLoginButton() {
  const router = useRouter();

  const handleSuccess = async (tokenResponse) => {
    // This is the access_token
    const accessToken = tokenResponse.access_token;
    console.log("Got Access Token from Google:", accessToken);

    try {
      // 5. Send this token to YOUR NEW backend endpoint
      const response = await fetch(
        "http://localhost:8000/api/auth/google/token",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          // Send the access_token
          body: JSON.stringify({ access_token: accessToken }),
        }
      );

      if (!response.ok) {
        throw new Error("Backend login failed");
      }

      // 6. Get your user data back from YOUR backend
      const userData = await response.json();
      console.log("Successfully logged in to our app:", userData);

      // Now you can save this user data (e.g., in React Context)
      // and redirect to the dashboard.
      // e.g., saveToAuthContext(userData);
      router.push("/dashboard");
    } catch (error) {
      console.error("Login failed:", error);
    }
  };

  const handleError = () => {
    console.log("Google Login Failed");
  };

  // 1. Initialize the hook
  const login = useGoogleLogin({
    onSuccess: handleSuccess,
    onError: handleError,
    // Note: This flow gives you an access_token.
    // The 'id_token' flow (PKCE) is more complex and requires
    // changing the backend to exchange a 'code' first.
  });

  return (
    <button
      onClick={() => login()} // 2. Call the login function on click
      className="flex cursor-pointer items-center justify-center w-full md:w-auto gap-3 px-2 md:px-5 py-2 border border-gray-300 dark:border-gray-700 rounded-lg 
                 bg-white dark:bg-gray-800 text-gray-800 dark:text-gray-100 
                 hover:bg-gray-50 dark:hover:bg-gray-700 transition-all duration-300 ease-in-out"
    >
      <FcGoogle size={20} />
      <span className="font-medium hidden md:flex text-sm 2xl:text-lg">
        ƒêƒÉng nh·∫≠p
      </span>
    </button>
  );
}
